<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config/index.json";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      selected: [], // Array reference
      options: [
      ],
      selected1: [], // Array reference
      options1: [
      ],
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      link:'',
      title:'',
      titleru:'',
      titleen:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      contact:[],
      imageUrl:[],
      myImages:[],
      files_name:[],
      imageUrl1:[],
      myImages1:[],
      files_name1:[],
      imageUrl2:[],
      myImages2:[],
      files_name2:[],
      description:'',
      descriptionru:'',
      descriptionen:'',
      short_description:'',
      short_descriptionru:'',
      short_descriptionen:'',
      durationen:'',
      durationru:'',
      duration:'',
      is_country:false,
      is_europe:false,
      is_world:false,
      is_main:false
    };
  },
  methods: {
    func(){
      this.is_world=!this.is_world
      console.log(this.is_world)
    },
    trySubmitFile(e) {
      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      this.imageUrl1=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile2(e) {
      this.imageUrl2=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name2.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages2.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl2.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },

    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      const form = new FormData();

      if(this.is_europe===true){
        for (var i=0;i<this.selected.length;i++){
          form.append(`common_programs`, this.selected[i]);
        }
        form.append("order", this.order);
        form.append("title", this.title);
        form.append("country", this.selected1);
        form.append("is_country", this.is_country);
        form.append("is_prof_education", this.is_europe);
        form.append("is_main", this.is_main);
        form.append("is_world", this.is_world);
        form.append("country", this.selected1);
      }
      else{
        form.append(`partner_id`, this.selected);
        form.append("icon", this.myImages[0]);
        form.append("duration", this.duration);
        form.append("description", this.description);
        form.append("short_intro", this.short_description);
        form.append("title", this.title);
        form.append("category_id", this.selected1);
        form.append("is_free", this.is_main);
      }

      axios.post(`${baseUrl}courses/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{
          this.title=''
          this.description=''
          this.order=''
          this.myImages=[]
          this.$toast("Ma'lumotlar o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru
          form.append("duration", this.durationru);
          form.append("description", this.descriptionru);
          form.append("short_intro", this.short_descriptionru);
          form.append("title", this.titleru);

          axios.patch(`${baseUrl}courses/${res.data.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{
              this.titleru=''
              this.descriptionru=''
              this.order=''
              this.myImages=[]
              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en
              form.append("duration", this.durationen);
              form.append("description", this.descriptionen);
              form.append("short_intro", this.short_descriptionen);
              form.append("title", this.titleen);

              axios.patch(`${baseUrl}courses/${res.data.id}/`,form,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{
                  this.titleen=''
                  this.descriptionen=''
                  this.order=''
                  this.myImages=[]
                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
        })
    },

  },
  mounted() {
    axios.get(`${baseUrl}course-partners/`)
      .then(res=>{
        res.data.forEach(el=>{
          console.log(el)
          this.options.push({
